import Certificate1 from "../assets/img/Certificate1.webp";
import Certificate2 from "../assets/img/Certificate2.webp";
import Certificate3 from "../assets/img/Certificate3.webp";
import Certificate4 from "../assets/img/Certificate4.webp";
import Certificate5 from "../assets/img/Certificate5.webp";
import Certificate6 from "../assets/img/Certificate6.webp";
import FsLightbox from "fslightbox-react";
import { Helmet, HelmetProvider } from "react-helmet-async";
import { Container } from "react-bootstrap";
import { useState } from "react";
import "./About.css";

function About() {
  const [lightboxController, setLightboxController] = useState({
    toggler: false,
    slide: 1,
  });

  function openLightboxOnSlide(number) {
    setLightboxController({
      toggler: !lightboxController.toggler,
      slide: number,
    });
  }

  return (
    <>
      <HelmetProvider>
        <Helmet>
          <title>Philemon NC - About</title>
        </Helmet>
      </HelmetProvider>
      <FsLightbox
        toggler={lightboxController.toggler}
        sources={[
          Certificate1,
          Certificate2,
          Certificate3,
          Certificate4,
          Certificate5,
          Certificate6,
        ]}
        slide={lightboxController.slide}
      />
      <Container fluid className="about-wrapper">
        <div className="about-left animate__animated animate__zoomIn">
          <h3>About</h3>
          <h4>
            &nbsp;&nbsp;Page <strong>02</strong>
          </h4>
        </div>
        <div className="about-right animate__animated animate__fadeIn animate__slower py-3">
          <p>
          Phil is having 3+ years of experience in industrial automation and about a year in software engineering. He is also an open source contributor and loves playing with new gadgets, tools and technologies . Current 127.0.0.1 == a  in the Kwale jungle.
          </p>

          <h4>Certificates</h4>
          <span></span>
          <div className="certificates-wrapper">
            <div className="row my-2">
              <div className="col certificates-ratio">
                <img
                  className="shadow"
                  loading="lazy"
                  src={Certificate1}
                  alt="Certificate 1"
                  onClick={() => openLightboxOnSlide(1)}
                />
              </div>
              <div className="col certificates-ratio gap-image">
                <img
                  className="shadow"
                  loading="lazy"
                  src={Certificate2}
                  alt="Certificate 2"
                  onClick={() => openLightboxOnSlide(2)}
                />
              </div>
              <div className="col certificates-ratio gap-image">
                <img
                  className="shadow"
                  loading="lazy"
                  src={Certificate3}
                  alt="Certificate 3"
                  onClick={() => openLightboxOnSlide(3)}
                />
              </div>
            </div>
            <div className="row my-4">
              <div className="col certificates-ratio">
                <img
                  className="shadow"
                  loading="lazy"
                  src={Certificate4}
                  alt="Certificate 4"
                  onClick={() => openLightboxOnSlide(4)}
                />
              </div>
              <div className="col certificates-ratio gap-image">
                <img
                  className="shadow"
                  loading="lazy"
                  src={Certificate5}
                  alt="Certificate 5"
                  onClick={() => openLightboxOnSlide(5)}
                />
              </div>
              <div className="col certificates-ratio gap-image">
                <img
                  className="shadow"
                  loading="lazy"
                  src={Certificate6}
                  alt="Certificate 6"
                  onClick={() => openLightboxOnSlide(6)}
                />
              </div>
            </div>
          </div>
        </div>
      </Container>
    </>
  );
}

export default About;
